import React from 'react';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { Form, Navbar, Container, Stack } from 'react-bootstrap';
import './App.css';

// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
import { getAnalytics, logEvent } from "firebase/analytics";

// Your web app's Firebase configuration
// For Firebase JS SDK v7.20.0 and later, measurementId is optional
const firebaseConfig = {
  apiKey: "AIzaSyDyDUWpJTtAlqgid5tCmr7_y6Yzj_IGqC4",
  authDomain: "nodelayers.firebaseapp.com",
  projectId: "nodelayers",
  storageBucket: "nodelayers.appspot.com",
  messagingSenderId: "662566877721",
  appId: "1:662566877721:web:b1c51bc2cd204bad3bc888",
  measurementId: "G-VREV4K3MNX"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);

const layers = require('./layers.json');

const regions = {
  'us-east-1': 'US East (N. Virginia)',
  'us-east-2': 'US East (Ohio)',
  'us-west-1': 'US West (N. California)',
  'us-west-2': 'US West (Oregon)',
  'eu-central-1': 'Europe (Frankfurt)',
  'eu-west-1': 'Europe (Ireland)',
  'eu-west-2': 'Europe (London)',
}

class NLNavbar extends React.Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    this.props.onRegionChange(e.target.value);
  }

  render() {
    return (
      <Navbar bg="dark" variant="dark" fixed="top">
        <Container>
          <Navbar.Brand href="/">
            <img
              alt=""
              src="/logo192.png"
              width="30"
              height="30"
              className="d-inline-block align-top"
            />{' '}
            NodeLayers
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse className="justify-content-end">
            <Navbar.Text>
              <Form.Select aria-label="AWS region" size="sm" value={this.props.region} onChange={this.handleChange}>
                {Object.keys(regions).map(region => {
                  return <option key={region} value={region}>{regions[region]} &middot; {region}</option>
                })}
              </Form.Select>
            </Navbar.Text>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    )
  }
}

class NLClipboardField extends React.Component {
  constructor(props) {
    super(props);
    this.handleCopied = this.handleCopied.bind(this);
    this.state = {
      copied: false
    };
  }

  handleCopied () {
    logEvent(analytics, 'select_content', {
      content_type: 'layer',
      item_id: this.handleCopied,
    });
    setTimeout(() => {
      this.setState({copied: false});
    }, 2500);
    this.setState({copied: true});
  }

  render () {
    return (
      <CopyToClipboard text={this.props.text} onCopy={this.handleCopied}>
        <div class="Copy form-control p-1 bg-light font-monospace">
          <Stack direction="horizontal">
            <div>{this.props.text}</div>
            <div className="ms-auto">{this.state.copied ? <span style={{color: 'red'}}>Copied</span> : null}</div>
          </Stack>
        </div>
      </CopyToClipboard>
    )
  }
}

function Layer(props) {
  const { name, description, version, keywords, region, regions } = props;
  return (
    <div class="pb-4 mb-4 border-bottom">
      <h5><a href={`https://npmjs.com/package/${name}`}>{name}</a> <span class="text-muted">@{version}</span></h5>
      <p>{description}</p>
      <div class="mb-3">
        {keywords && keywords.map(keyword =>
          <span class="badge text-bg-light">{keyword}</span>
        )}
      </div>
      <NLClipboardField text={regions && regions[region]}></NLClipboardField>
    </div>
  )
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.handleRegionChange = this.handleRegionChange.bind(this);
    this.state = { region: 'us-east-1' };
  }

  handleRegionChange(region) {
    this.setState({ region });
  }

  render() {
    return (
      <div>
        <NLNavbar region={this.state.region} onRegionChange={this.handleRegionChange} />

        <main class="container mt-5">
          <div class="py-5 mb-4 border-bottom text-center ">
            <h4 class="fw-light">Easily <span class="badge text-bg-light font-monospace">import</span> NodeJS modules in AWS
              Lambda functions</h4>
            <h5 class="fw-light">Just copy the layer ARN and add to your function</h5>
          </div>
          {Object.keys(layers).sort().map(name => {
            const layer = layers[name]
            return (
              <Layer key={name} name={name} version={layer.version} description={layer.description} keywords={layer.keywords} region={this.state.region} regions={layer._regions} />
            )
          })}
        </main>

        <div class="container">
          <footer class="pt-5 my-5 text-muted">
            Made by <a class="text-decoration-none" href="https://www.twitter.com/gazoakley">@gazoakley</a>. Inspired by <a class="text-decoration-none" href="https://github.com/keithrozario/Klayers">Klayers</a>.
          </footer>
        </div>
      </div>
    );
  }
}

export default App;
